body {
  margin: 0;
  padding: 0;
  font-family:"raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-nav{
	padding:30px;
/*	height:300px;*/
	background:#95ACA8;
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	right:0;
	width:70%;
	max-width:400px;
	width:350px;
	z-index:200;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
	border-bottom-left-radius: 200px;
}

.hide{
	-webkit-transform:translateX(+100%);
	        transform:translateX(+100%);
}

.bar{
	position:fixed;
	z-index:200;
	top:10px;
	right:10px;
}




.name-style{
	font-size:80px;
}

.dottedBorder{
  border:none;
  border-top:8px dotted gray;
  color:#fff;
}

.show{
  display:block;
  -webkit-animation-name: showit;
  -webkit-animation-duration: 5s;
}

.link-effect{
  -webkit-animation-name: grow-effect;
  -webkit-animation-duration: 2s;
  transition: all .2s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.link-effect:hover{
   -webkit-animation-name: none;
}

 @-webkit-keyframes grow-effect{
 from {-webkit-transform: scale(1);transform: scale(1);}
 50%{-webkit-transform: scale(1.2);transform: scale(1.2);}
  to {-webkit-transform: scale(1);transform: scale(1);}
}

@-webkit-keyframes showit {
  from {opacity:0;}
  to {opacity:1;}
}


@media(min-width:770px){
	.hidelink{
		display:none;
	}

}

.container {
  position: relative;
}

.container:hover .text {
  opacity: 1;

}
.text:hover{
  color:#FFFCEB;
  background:#95ACA8;
  border:none;
}

.container:hover .blurImage{
    opacity:.1;
    transition: .4s ease-in; 
}
.text {
  transition: .4s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
  color:#727E7C;
}
.bgc{
  background:rgba(255,255,255,.3);
}
@media(max-width:600px){
  .projectImage{
    width:300px;
    height:230px;
  }
 
}
input:focus {
    outline:none;
}

textarea:focus {
    outline:none;
}

.App {
  background:#D5E2D9;
}

.scrollbar{
    overflow-y:scroll;
    overflow-x:hidden;
}

#style-1::-webkit-scrollbar {
    width: 0px;
} 

.icon i {
 font-size:4rem;
 margin:20px;
 padding:10px;
}

.backdrop {
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background:rgba(0,0,0,0.4);
	z-index:100;
}

.shadowE{
  box-shadow:0px 15px 10px -15px rgba(0,0,0,0.5);

}

@media(max-width:769px){
  .space{
    display:none;
  }

}

