.container {
  position: relative;
}

.container:hover .text {
  opacity: 1;

}
.text:hover{
  color:#FFFCEB;
  background:#95ACA8;
  border:none;
}

.container:hover .blurImage{
    opacity:.1;
    transition: .4s ease-in; 
}
.text {
  transition: .4s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
  color:#727E7C;
}
.bgc{
  background:rgba(255,255,255,.3);
}
@media(max-width:600px){
  .projectImage{
    width:300px;
    height:230px;
  }
 
}