.App {
  background:#D5E2D9;
}

.scrollbar{
    overflow-y:scroll;
    overflow-x:hidden;
}

#style-1::-webkit-scrollbar {
    width: 0px;
} 

.icon i {
 font-size:4rem;
 margin:20px;
 padding:10px;
}

.backdrop {
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background:rgba(0,0,0,0.4);
	z-index:100;
}

.shadowE{
  box-shadow:0px 15px 10px -15px rgba(0,0,0,0.5);

}

@media(max-width:769px){
  .space{
    display:none;
  }

}
